.new-arrivals {
  &__head {
    margin-bottom: 55px;
    border-bottom: 2px solid $gray-400;
  }

  .section__heading {
    margin-bottom: 0;
    line-height: 1;
  }

  &__swiper {
    overflow-x: hidden;
  }

  &__slide {
    flex-shrink: 0;
  }

  &__slide-heading {
    display: block;
    margin-bottom: 35px;
    font-size: 25px;
    font-weight: bold;
  }
}
