.catalog-item {
  position: relative;
  display: flex;
  flex-shrink: 0;
  flex-wrap: wrap;
  margin-bottom: 20px;
  border: 1px solid $gray-400;
  text-decoration: none;

  &__link {
    display: block;
    width: 100%;
    text-decoration: none;
  }

  &__image-wrapper {
    position: relative;
    width: 100%;
    margin-bottom: 20px;
    padding-top: 66.6666%;
  }

  &__image {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 100%;
    width: 100%;
    max-height: 100%;
    height: 100%;
    object-fit: cover;
    transform: translate(-50%,-50%);
  }

  &__stickers {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    position: absolute;
    top: 0;
    right: 0;
    padding-left: 40px;

    .sticker {
      margin-bottom: 5px;

      &:not(:first-child) {
        margin-left: 8px;
      }
    }
  }

  &__name {
    display: block;
    margin-right: 30px;
    margin-bottom: 10px;
    margin-left: 30px;
    color: $gray-700;
    font-size: 15px;
    font-weight: bold;
    text-align: center;
  }

  &__small-text {
    color: $gray-600;
    font-size: 13px;
  }

  &__type {
    display: inline-block;
    width: calc(50% - 20px);
    margin-right: auto;
    margin-bottom: 18px;
    margin-left: 20px;
    text-align: left;
  }

  &__code {
    display: inline-block;
    width: calc(50% - 20px);
    margin-right: 20px;
    margin-bottom: 18px;
    text-align: right;
  }

  .price {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    width: 100%;
    margin-right: 20px;
    margin-bottom: 25px;
    margin-left: 20px;
    text-align: center;

    &__value {
      &:only-child {
        display: block;
        margin-right: auto;
        margin-left: auto;
      }
    }
  }

  .items-counter {
    max-width: calc(100% - 100px);
    margin-right: 20px;
    margin-bottom: 20px;
    margin-left: 20px;
  }

  .input-field_underlined {
    max-width: 42%;
  }

  .catalog-item__buy {
    margin-right: 20px;
    margin-left: auto;
  }

  &__quick-view {
    display: none;
    position: absolute;
    top: 78px;
    left: 50%;
    max-width: none;
    width: auto;
    transform: translate(-50%, -50%);

    .button__text {
      white-space: nowrap;
    }
  }

  .button_heart {
    position: absolute;
    top: 0;
    left: 0;
  }

  &:hover {
    box-shadow: 2px 10px 16px 0px rgba($black, 0.1);

    & > .catalog-item__quick-view {
      display: flex;
    }
  }
}
