.rating {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 20px;
  unicode-bidi: bidi-override;
  direction: rtl;
  text-align: center;
}

.rating__star {
  display: inline-block;
  position: relative;
  width: 1.1em;
  color: $gray-500;
  cursor: pointer;
}

.rating__star:hover,
.rating__star:hover ~ .rating__star {
  color: transparent;
}

.rating__star:hover:before,
.rating__star:hover ~ .rating__star:before {
  content: "\2605";
  position: absolute;
  left: 0;
  color: $orange-100;
}
